function compare (a, b) {
	if (Array.isArray(a) && Array.isArray(b)) {
		const l = Math.max(a.length, b.length);
		const sa = l - a.length;
		const sb = l - b.length;
		for (let i = 0; i < l; i++) {
			const av = i >= sa ? a[i - sa] : 0;
			const bv = i >= sb ? b[i - sb] : 0;
			if (av > bv) {
				return 1;
			}
			else if (av < bv) {
				return -1;
			}
		}
		return 0;
	}
	else {
		return a > b ? 1 : a < b ? -1 : 0;
	}
}

function isInt (n) {
	return n % 1 === 0;
}


function inc (val, count = 1) {
	if (Array.isArray(val)) {
		const MAX = 65536;
		let i = count;
		for (let p = val.length - 1; p >= 0; p--) {
			const res = val[p] + i;
			val[p] = res % MAX;
			if (val[p] < 0) {
				val[p] = MAX + val[p];
			}
			val[p] = Math.abs(val[p]);
			i = Math.floor(res / MAX);
		}
		if (i > 0) {
			val.unshift(i);
		}
		return val;
	}
	else {
		return val + count;
	}
}


function sqnc (from, to, step = 1, count) {
	function isFiniteNumber (value) {
		return Number(value) === value && isFinite(value);
	}

	const result = [];

	if (typeof from === "function" && isFinite(to)) {
		let val = 0;
		for (let n = 0; n < to; n++) {
			val = from(n, val, result);
			result.push(val);
		}
		return result;
	}

	if (to != null) {
		count = null;
		console.warn(`"count" argument is ignored when "to" argument is specified`);
	}

	const countMode = count != null;
	if (countMode || (from != null && to != null && step != null)) {
		let chars = false;
		const initial = from;

		if (typeof from === "string") {
			from = from.split("").map(s => s.charCodeAt(0));
			chars = true;
			if (step && typeof step !== "function" && !isInt(step)) {
				throw new Error("Step should be integer when characters are used");
			}
		}

		if (countMode) {
			if (count === 0) {
				throw new Error("count can't be zero!");
			}
			if (!isFiniteNumber(count)) {
				throw new Error("count should be a number!");
			}

			count = Math.abs(count);

			let c = count;
			let val = from;

			if (!step) {
				while (c) {
					result.push(initial);
					c--;
				}
			}
			else {
				if (!chars && !isFiniteNumber(from)) {
					throw new Error("\"from\" argument should be number or character when \"step\" and \"count\" specified!");
				}

				let n = 0;
				while (c) {
					n++;
					result.push(chars ? String.fromCharCode.apply(null, val) : val);
					val = inc(val, typeof step === "function" ? step(n, val) : step);
					c--;
				}
			}
		}
		else {
			if (typeof to === "string") {
				to = to.split("").map(s => s.charCodeAt(0));
				chars = true;
			}

			if (
				(!Array.isArray(from) && !isFiniteNumber(from)) ||
				(!Array.isArray(to) && !isFiniteNumber(to)) ||
				(!isFiniteNumber(step) && typeof step !== "function")
			) {
				throw new Error("arguments should be finite!");
			}
			if (typeof step !== "function") {
				step = Math.abs(step);
				if (step === 0) {
					throw new Error("\"step\" argument can't be zero!");
				}
			}

			let dir = 1;
			let check;
			if (compare(from, to) > 0) {
				dir = -1;
				check = a => compare(a, to) >= 0;
			}
			else {
				check = a => compare(a, to) <= 0;
			}
			let n = 0;
			let val = from;
			while (check(val)) {
				n++;
				result.push(chars ? String.fromCharCode.apply(null, val) : val);
				val = inc(val, dir * (typeof step === "function" ? step(n, val, result) : step));
			}
		}
	}
	return result;
}

sqnc.maxSize = 65536;

export default sqnc;



